import React, { useEffect, useState } from 'react';
import { api } from '../api/api';
import { useParams, useNavigate } from 'react-router-dom';
import { Button, Form, Spinner, Alert } from 'react-bootstrap';

export default function UserDetailAdmin() {
  const { id } = useParams();
  const [user, setUser] = useState(null);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    api.get(`/api/users/${id}`)
      .then(res => setUser(res.data))
      .catch(() => setError('Khng tm thy user!'));
  }, [id]);

  const handleChangeRole = async e => {
    setSaving(true);
    try {
      await api.put(`/api/users/${id}/roles`, { roles: [e.target.value] });
      setUser(u => ({ ...u, roles: [e.target.value] }));
    } catch {
      alert('i vai tr tht bi!');
    }
    setSaving(false);
  };

  const handleToggleLock = async () => {
    setSaving(true);
    try {
      if (user.isLocked) {
        await api.patch(`/api/users/${id}/unlock`);
        setUser(u => ({ ...u, isLocked: false }));
      } else {
        await api.patch(`/api/users/${id}/lock`);
        setUser(u => ({ ...u, isLocked: true }));
      }
    } catch {
      alert('Thao tc tht bi!');
    }
    setSaving(false);
  };

  if (error) return <Alert variant="danger">{error}</Alert>;
  if (!user) return <Spinner animation="border" />;

  return (
    <div>
      <h4>Chi tit ngi dng</h4>
      <div>Email: <b>{user.email}</b></div>
      <div className="my-2">
        Vai tr:
        <Form.Select
          value={user.roles[0] || ''}
          onChange={handleChangeRole}
          disabled={saving}
          style={{ width: 160, display: 'inline-block', marginLeft: 8 }}
        >
          <option value="Admin">Admin</option>
          <option value="Customer">Customer</option>
        </Form.Select>
      </div>
      <div className="my-2">
        Trng thi:&nbsp;
        {user.isLocked
          ? <span className="badge bg-danger"> kha</span>
          : <span className="badge bg-success">Hot ng</span>
        }
        &nbsp;
        <Button
          variant={user.isLocked ? 'success' : 'danger'}
          size="sm"
          onClick={handleToggleLock}
          disabled={saving}
        >
          {user.isLocked ? 'M kha' : 'Kha'}
        </Button>
      </div>
      <Button className="mt-3" onClick={() => navigate(-1)}>Quay li</Button>
    </div>
  );
}
