import React, { useState } from 'react';
import { useNavigate, useLocation, Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { toast } from 'react-toastify';

export default function LoginPage() {
  const nav = useNavigate();
  const location = useLocation();
  const { login } = useAuth();

  const [form, setForm] = useState({ email: '', password: '' });
  const [loading, setLoading] = useState(false);

  const handleChange = e => {
    setForm(f => ({ ...f, [e.target.name]: e.target.value }));
  };

  const handleSubmit = async e => {
    e.preventDefault();
    if (!form.email) {
      toast.error('Vui lng nhp email!');
      return;
    }
    if (!form.password) {
      toast.error('Vui lng nhp mt khu!');
      return;
    }
    setLoading(true);
    try {
      await login(form.email, form.password);
      toast.success('ng nhp thnh cng!');
      const from = location.state?.from || '/';
      nav(from, { replace: true });
    } catch (e) {
      toast.error(e.response?.data?.detail || 'ng nhp tht bi!');
    }
    setLoading(false);
  };

  return (
    <div className="container" style={{ maxWidth: 400 }}>
      <h2 className="mb-4">ng nhp</h2>
      <form onSubmit={handleSubmit}>
        <div className="mb-3">
          <label>Email</label>
          <input
            name="email"
            type="email"
            className="form-control"
            value={form.email}
            onChange={handleChange}
            autoFocus
            autoComplete="username"
            disabled={loading}
          />
        </div>
        <div className="mb-3">
          <label>Mt khu</label>
          <input
            name="password"
            type="password"
            className="form-control"
            value={form.password}
            onChange={handleChange}
            autoComplete="current-password"
            disabled={loading}
          />
        </div>
        <button
          type="submit"
          className="btn btn-primary w-100"
          disabled={loading}
        >
          {loading ? 'ang ng nhp...' : 'ng nhp'}
        </button>
        {/* Thm hai link tin dng bn di */}
        <div className="d-flex justify-content-between mt-3">
          <Link to="/register">ng k ti khon mi</Link>
          <Link to="/forgot-password">Qun mt khu?</Link>
        </div>
      </form>
    </div>
  );
}
