// File: src/components/CategoryForm.jsx

import React, { useState, useEffect } from 'react';
import { api } from '../api/api';
import { useNavigate, useParams } from 'react-router-dom';

export default function CategoryForm() {
  const { id } = useParams();
  const isEdit = Boolean(id);
  const navigate = useNavigate();
  const [name, setName] = useState('');

  useEffect(() => {
    if (!isEdit) return;

    // nh ngha async function bn trong effect
    const fetchCategory = async () => {
      try {
        const res = await api.get(`/api/categories/${id}`);
        setName(res.data.name);
      } catch {
        alert('Khng tm thy danh mc');
        navigate('/admin/categories', { replace: true });
      }
    };

    // Gi ngay
    fetchCategory();
  }, [isEdit, id, navigate]);

  const handleSubmit = async e => {
    e.preventDefault();

    const dto = isEdit
      ? { id: Number(id), name }
      : { name };

    try {
      if (isEdit) {
        await api.put(`/api/categories/${id}`, dto);
      } else {
        await api.post('/api/categories', dto);
      }
      navigate('/admin/categories');
    } catch {
      alert('Li khi lu danh mc');
    }
  };

  return (
    <div className="card mx-auto" style={{ maxWidth: '500px' }}>
      <div className="card-body">
        <h5 className="card-title">
          {isEdit ? 'Chnh sa' : 'Thm mi'} danh mc
        </h5>
        <form onSubmit={handleSubmit}>
          <div className="mb-3">
            <label className="form-label">Tn danh mc</label>
            <input
              type="text"
              className="form-control"
              value={name}
              onChange={e => setName(e.target.value)}
              required
            />
          </div>
          <button type="submit" className="btn btn-primary">
            {isEdit ? 'Cp nht' : 'Thm'}
          </button>
          <button
            type="button"
            className="btn btn-secondary ms-2"
            onClick={() => navigate('/admin/categories')}
          >
            Hu
          </button>
        </form>
      </div>
    </div>
  );
}
