import React, { useState } from 'react';
import { useCart } from '../contexts/CartContext';
import { api } from '../api/api';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';

export default function CheckoutPage() {
  const { cart, clearCart } = useCart();
  const [name, setName] = useState('');
  const [address, setAddress] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = async e => {
    e.preventDefault();
    if (!name || !address) {
      toast.error('Vui lng nhp y  thng tin giao hng!');
      return;
    }
    setLoading(true);
    try {
      // GI NG NH DNG CHO BACKEND
      await api.post('/api/orders', {
        CustomerName: name,
        CustomerAddress: address,
        Items: cart.items.map(i => ({
          ProductId: i.id,       // NG key backend OrderItem yu cu
          Quantity: i.quantity,
          UnitPrice: i.price     // Gi lun gi (nu backend dng)
        }))
      });
      toast.success('t hng thnh cng!');
      clearCart();
      navigate('/myorders');
    } catch (err) {
      toast.error('t hng tht bi!');
    }
    setLoading(false);
  };

  if (cart.items.length === 0)
    return <div className="alert alert-info">Gi hng ca bn ang trng.</div>;

  return (
    <div>
      <h3>Thng tin giao hng</h3>
      <form onSubmit={handleSubmit} style={{ maxWidth: 400 }}>
        <div className="mb-3">
          <label>H tn</label>
          <input className="form-control" value={name} onChange={e => setName(e.target.value)} disabled={loading} />
        </div>
        <div className="mb-3">
          <label>a ch</label>
          <input className="form-control" value={address} onChange={e => setAddress(e.target.value)} disabled={loading} />
        </div>
        <button className="btn btn-success" type="submit" disabled={loading}>
          {loading ? 'ang t hng...' : 't hng'}
        </button>
      </form>
    </div>
  );
}
