import React from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider } from './contexts/AuthContext';
import { CartProvider } from './contexts/CartContext';

import Navbar from './components/Navbar';
import LoginPage from './components/LoginPage';
import PrivateRoute from './components/PrivateRoute';
import CategoryMasterDetail from './components/CategoryMasterDetail';
import CheckoutPage from './components/CheckoutPage';
import CartPage from './components/CartPage';

import CategoryListAdmin from './components/CategoryListAdmin';
import CategoryForm      from './components/CategoryForm';

import ProductListAdmin from './components/ProductListAdmin';
import ProductForm      from './components/ProductForm';

import OrderListAdmin   from './components/OrderListAdmin';
import OrderDetailAdmin from './components/OrderDetailAdmin';

import RegisterPage from './components/RegisterPage';
import MyOrdersPage from './components/MyOrdersPage';

import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import UserListAdmin from './components/UserListAdmin';
import UserDetailAdmin from './components/UserDetailAdmin';

import ForgotPasswordPage from './components/ForgotPasswordPage';
import ResetPasswordPage from './components/ResetPasswordPage';

export default function App() {
  return (
    <AuthProvider>
      <CartProvider>
        <Navbar />

        <div className="container my-4">
          <Routes>
            {/* Public */}
            <Route path="/" element={<CategoryMasterDetail />} />
            <Route path="/login" element={<LoginPage />} />
            <Route path="/cart" element={<CartPage />} />
            <Route path="/checkout" element={<PrivateRoute><CheckoutPage /></PrivateRoute>} />

            {/* Admin: Categories */}
            <Route path="/admin/categories" element={
              <PrivateRoute><CategoryListAdmin/></PrivateRoute>
            }/>
            <Route path="/admin/categories/new" element={
              <PrivateRoute><CategoryForm/></PrivateRoute>
            }/>
            <Route path="/admin/categories/:id/edit" element={
              <PrivateRoute><CategoryForm/></PrivateRoute>
            }/>

            {/* Admin: Products */}
            <Route path="/admin/products" element={
              <PrivateRoute><ProductListAdmin/></PrivateRoute>
            }/>
            <Route path="/admin/products/new" element={
              <PrivateRoute><ProductForm/></PrivateRoute>
            }/>
            <Route path="/admin/products/:id/edit" element={
              <PrivateRoute><ProductForm/></PrivateRoute>
            }/>

            {/* Admin: Orders */}
            <Route path="/admin/orders" element={
              <PrivateRoute><OrderListAdmin/></PrivateRoute>
            }/>
            <Route path="/admin/orders/:id" element={
              <PrivateRoute><OrderDetailAdmin/></PrivateRoute>
            }/>

            {/* Fallback */}
            <Route path="*" element={<Navigate to="/" replace />} />
            <Route path="/register" element={<RegisterPage />} />
            <Route path="/myorders" element={
                <PrivateRoute>
                  <MyOrdersPage />
                </PrivateRoute>
            } />        
            <Route path="/admin/users" element={
              <PrivateRoute><UserListAdmin /></PrivateRoute>
            } />
            <Route path="/admin/users/:id" element={
              <PrivateRoute><UserDetailAdmin /></PrivateRoute>
            } />
            <Route path="/forgot-password" element={<ForgotPasswordPage />} />
            <Route path="/reset-password" element={<ResetPasswordPage />} />
          </Routes>          
        </div>
        {/* ToastContainer nn t  y, ngoi Routes */}
        <ToastContainer position="top-center" autoClose={1800} />
        </CartProvider> 
    </AuthProvider>
  );
}
