// File: src/components/CategoryListAdmin.jsx

import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { api } from '../api/api';

export default function CategoryListAdmin() {
  const [cats, setCats] = useState([]);
  const navigate = useNavigate();

  // ---------- EFFECT AN TON ----------
  useEffect(() => {
    let isMounted = true;         // flag  trnh setState khi component  unmount

    async function fetchCategories() {
      try {
        const res = await api.get('/api/categories');
        if (isMounted) setCats(res.data);
      } catch {
        alert('Khng ti c danh mc');
      }
    }

    fetchCategories();

    // cleanup ch return hm
    return () => {
      isMounted = false;
    };
  }, []); // ch chy mt ln

  const refresh = () => {
    api.get('/api/categories')
       .then(res => setCats(res.data))
       .catch(() => alert('Khng ti c danh mc'));
  };

  const onDelete = async id => {
    if (!window.confirm('Xc nhn xo?')) return;
    try {
      await api.delete(`/api/categories/${id}`);
      refresh();
    } catch {
      alert('Xo tht bi');
    }
  };

  return (
    <div>
      <div className="d-flex justify-content-between align-items-center mb-3">
        <h3>Qun l danh mc</h3>
        <button
          className="btn btn-primary"
          onClick={() => navigate('/admin/categories/new')}
        >
          Thm danh mc
        </button>
      </div>

      <table className="table table-bordered">
        <thead>
          <tr>
            <th style={{ width: '80px' }}>ID</th>
            <th>Tn danh mc</th>
            <th style={{ width: '150px' }}>Hnh ng</th>
          </tr>
        </thead>
        <tbody>
          {cats.map(c => (
            <tr key={c.id}>
              <td>{c.id}</td>
              <td>{c.name}</td>
              <td>
                <button
                  className="btn btn-sm btn-info me-2"
                  onClick={() => navigate(`/admin/categories/${c.id}/edit`)}
                >
                  Sa
                </button>
                <button
                  className="btn btn-sm btn-danger"
                  onClick={() => onDelete(c.id)}
                >
                  Xo
                </button>
              </td>
            </tr>
          ))}
          {cats.length === 0 && (
            <tr>
              <td colSpan="3" className="text-center text-muted">
                Cha c danh mc no.
              </td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  );
}
