import React, { useEffect, useState } from 'react';
import { api } from '../api/api';
import { useParams, useNavigate } from 'react-router-dom';

export default function OrderDetailAdmin() {
  const { id } = useParams();
  const nav = useNavigate();
  const [order, setOrder] = useState(null);

  useEffect(() => {
    api
      .get(`/api/orders/${id}`)
      .then(res => setOrder(res.data))
      .catch(() => {
        alert('Khng tm thy n hng');
        nav('/admin/orders');
      });
  }, [id, nav]);

  if (!order) return null;

  const total = order.items.reduce(
    (sum, i) => sum + i.quantity * i.unitPrice,
    0
  );

  return (
    <div>
      <h3>n hng #{order.id}</h3>
      <p>Khch: {order.customerName}</p>
      <p>a ch: {order.customerAddress}</p>
      <p>Ngy: {new Date(order.createdAt).toLocaleString()}</p>
      <table className="table">
        <thead>
          <tr>
            <th>Sn phm</th>
            <th>SL</th>
            <th>Gi</th>
            <th>Thnh tin</th>
          </tr>
        </thead>
        <tbody>
          {order.items.map(i => (
            <tr key={i.id}>
              <td>{i.product.name}</td>
              <td>{i.quantity}</td>
              <td>{i.unitPrice.toLocaleString()}</td>
              <td>
                {(i.quantity * i.unitPrice).toLocaleString()}
              </td>
            </tr>
          ))}
          <tr>
            <td colSpan="3" className="text-end">
              <b>Tng</b>
            </td>
            <td>
              <b>{total.toLocaleString()}</b>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}
