// File: src/components/CategoryMasterDetail.jsx

import React, { useEffect, useState } from 'react';
import { api } from '../api/api';
import ProductCard from './ProductCard';
import { useSearchParams } from 'react-router-dom';

export default function CategoryMasterDetail() {
  const [sp] = useSearchParams();
  const search   = sp.get('search');
  const priceMin = sp.get('priceMin');
  const priceMax = sp.get('priceMax');

  const [cats, setCats] = useState([]);
  const [prods, setProds] = useState([]);
  const [sel, setSel] = useState(null);
  const [lc, slc] = useState(true);
  const [lp, slp] = useState(false);

  // Load categories
  useEffect(() => {
    slc(true);
    api.get('/api/categories')
      .then(r => {
        setCats(r.data);
        if (r.data.length) setSel(r.data[0].id);
      })
      .finally(() => slc(false));
  }, []);

  // Load products on sel/search/min/max change
  useEffect(() => {
    if (sel == null) return;
    slp(true);
    const qs = new URLSearchParams();
    qs.set('categoryId', sel);
    if (search)    qs.set('search', search);
    if (priceMin)  qs.set('priceMin', priceMin);
    if (priceMax)  qs.set('priceMax', priceMax);

    api.get(`/api/products?${qs.toString()}`)
      .then(r => setProds(r.data))
      .finally(() => slp(false));
  }, [sel, search, priceMin, priceMax]);

  if (lc) return <p className="text-center text-muted">ang ti danh mc</p>;

  return (
    <div className="row">
      <aside className="col-12 col-md-3 mb-4">
        <div className="list-group">
          {cats.map(c => (
            <button
              key={c.id}
              type="button"
              className={'list-group-item list-group-item-action ' + (c.id === sel ? 'active' : '')}
              onClick={() => setSel(c.id)}
            >
              {c.name}
            </button>
          ))}
        </div>
      </aside>

      <main className="col-12 col-md-9">
        {lp ? (
          <p className="text-center text-muted">ang ti sn phm</p>
        ) : prods.length ? (
          <div className="row g-4">
            {prods.map(p => (
              <div key={p.id} className="col-12 col-sm-6 col-lg-4">
                <ProductCard product={p} />
              </div>
            ))}
          </div>
        ) : (
          <p className="text-center text-muted">Khng tm thy sn phm.</p>
        )}
      </main>
    </div>
  );
}
