// src/components/ResetPasswordPage.jsx
import React, { useState } from 'react';
import { api } from '../api/api';
import { useSearchParams } from 'react-router-dom';
import { Form, Button, Alert, Spinner } from 'react-bootstrap';

export default function ResetPasswordPage() {
  const [params] = useSearchParams();
  const email = params.get('email') || '';
  const token = params.get('token') || '';
  const [pw, setPw] = useState('');
  const [done, setDone] = useState(false);
  const [err, setErr] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = async e => {
    e.preventDefault();
    setLoading(true); setErr('');
    try {
      await api.post('/api/auth/reset-password', { email, token, newPassword: pw });
      setDone(true);
    } catch (e) {
      setErr('Khng t li c mt khu! Token  ht hn hoc c li khc.');
    }
    setLoading(false);
  };

  if (!email || !token) return <Alert variant="danger">Link t li mt khu khng hp l!</Alert>;

  return (
    <div className="mx-auto" style={{maxWidth: 420, marginTop: 40}}>
      <h4>t li mt khu mi</h4>
      {done
        ? <Alert variant="success">Bn  i mt khu thnh cng! Hy <a href="/login">ng nhp li</a>.</Alert>
        : <Form onSubmit={handleSubmit}>
            <Form.Group>
              <Form.Label>Mt khu mi</Form.Label>
              <Form.Control
                type="password"
                required
                minLength={6}
                value={pw}
                onChange={e => setPw(e.target.value)}
                autoFocus
              />
            </Form.Group>
            <Button type="submit" className="mt-3" disabled={loading}>
              {loading ? <Spinner size="sm" /> : "t li mt khu"}
            </Button>
            {err && <Alert variant="danger" className="mt-2">{err}</Alert>}
          </Form>
      }
    </div>
  );
}
