import React, { useState, useEffect } from 'react';
import { api } from '../api/api';
import { Link } from 'react-router-dom';

export default function ProductListAdmin() {
  const [products, setProducts] = useState([]);

  const fetchData = () => {
    api.get('/api/products')
      .then(res => setProducts(res.data))
      .catch(err => {
        console.error('Li ti sn phm:', err);
        alert('Khng th ti danh sch sn phm');
      });
  };

  useEffect(() => {
    fetchData();
  }, []);

  const handleDelete = id => {
    if (!window.confirm('Xc nhn xo sn phm?')) return;
    api.delete(`/api/products/${id}`)
      .then(() => fetchData())
      .catch(err => {
        console.error('Li xo sn phm:', err);
        alert('Xo khng thnh cng');
      });
  };

  return (
    <>
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h2>Qun l sn phm</h2>
        <Link to="/admin/products/new" className="btn btn-success">
          Thm sn phm
        </Link>
      </div>

      <table className="table table-striped">
        <thead>
          <tr>
            <th style={{ width: '5%' }}>ID</th>
            <th style={{ width: '30%' }}>Tn</th>
            <th style={{ width: '20%' }}>Gi</th>
            <th style={{ width: '20%' }}>nh</th>
            <th style={{ width: '25%' }}>Hnh ng</th>
          </tr>
        </thead>
        <tbody>
          {products.length > 0 ? (
            products.map(p => (
              <tr key={p.id}>
                <td>{p.id}</td>
                <td>{p.name}</td>
                <td>{p.price.toLocaleString()}</td>
                <td>
                  <img
                    src={p.imageUrl}
                    alt={p.name}
                    style={{ height: '50px', objectFit: 'cover' }}
                  />
                </td>
                <td>
                  <Link
                    to={`/admin/products/${p.id}/edit`}
                    className="btn btn-sm btn-primary me-2"
                  >
                    Sa
                  </Link>
                  <button
                    className="btn btn-sm btn-danger"
                    onClick={() => handleDelete(p.id)}
                  >
                    Xo
                  </button>
                </td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="5" className="text-center">
                Cha c sn phm no
              </td>
            </tr>
          )}
        </tbody>
      </table>
    </>
  );
}
