import React from 'react';
import { Navbar, Nav, NavDropdown, Container } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useCart } from '../contexts/CartContext';

export default function MyNavbar() {
  const { token, logout, getRole } = useAuth();
  const role = getRole();
  const nav = useNavigate();
  const { cart } = useCart();
  const cartCount = cart?.items?.reduce((sum, i) => sum + i.quantity, 0) || 0;

  const handleLogout = () => {
    logout();
    nav('/login');
  };

  return (
    <Navbar bg="light" expand="lg" className="mb-4">
      <Container>
        <Navbar.Brand as={Link} to="/">Orchid Store</Navbar.Brand>
        <Navbar.Toggle aria-controls="main-navbar-nav" />
        <Navbar.Collapse id="main-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/">Shop</Nav.Link>
            {token && role === 'Admin' && (
              <NavDropdown title="Admin" id="admin-dropdown">
                <NavDropdown.Item as={Link} to="/admin/products">Sn phm</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/admin/categories">Danh mc</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/admin/orders">n hng</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/admin/users">Ngi dng</NavDropdown.Item>
              </NavDropdown>
            )}
          </Nav>
          <Nav className="ms-auto">
            <Nav.Link as={Link} to="/cart">
              Gi hng {cartCount > 0 && <span className="badge bg-success">{cartCount}</span>}
            </Nav.Link>
            {token && (
              <Nav.Link as={Link} to="/myorders">n hng ca ti</Nav.Link>
            )}
            {!token ? (
              <Nav.Link as={Link} to="/login">ng nhp</Nav.Link>
            ) : (
              <Nav.Link as="button" style={{ border: 0, background: "none" }} onClick={handleLogout}>ng xut</Nav.Link>
            )}
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}
