import React, { useEffect, useState } from 'react';
import { api } from '../api/api';
import { Table, Spinner, Alert } from 'react-bootstrap';
import { Link } from 'react-router-dom';

export default function UserListAdmin() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    api.get('/api/users')
      .then(res => setUsers(res.data))
      .catch(() => setError('Khng th ti danh sch ngi dng!'))
      .finally(() => setLoading(false));
  }, []);

  return (
    <div>
      <h3>Danh sch ngi dng</h3>
      {loading ? (
        <Spinner animation="border" />
      ) : error ? (
        <Alert variant="danger">{error}</Alert>
      ) : (
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>#</th>
              <th>Email</th>
              <th>Roles</th>
              <th>Trng thi</th>
              <th>Thao tc</th>
            </tr>
          </thead>
          <tbody>
            {users.map((u, idx) => (
              <tr key={u.id}>
                <td>{idx + 1}</td>
                <td>{u.email}</td>
                <td>{u.roles.join(', ')}</td>
                <td>
                  {u.isLocked
                    ? <span className="badge bg-danger"> kha</span>
                    : <span className="badge bg-success">Hot ng</span>}
                </td>
                <td>
                  <Link to={`/admin/users/${u.id}`}>
                    <button className="btn btn-sm btn-outline-primary">Xem</button>
                  </Link>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      )}
    </div>
  );
}
