// File: src/components/ProductForm.jsx
import React, { useEffect, useState } from 'react';
import { api } from '../api/api';
import { useNavigate, useParams } from 'react-router-dom';

export default function ProductForm() {
  const { id }      = useParams();
  const isEdit      = Boolean(id);
  const navigate    = useNavigate();

  const [cats, setCats] = useState([]);
  const [form, setForm] = useState({
    name:'', description:'', priceRaw:'', priceNum:0,
    categoryId:'', imageFile:null, imageUrl:''
  });

  /*  ti danh mc & sn phm  */
  useEffect(() => {
    api.get('/api/categories').then(r => setCats(r.data));

    if (isEdit) {
      api.get(`/api/products/${id}`).then(r => {
        const p = r.data;
        setForm(f => ({
          ...f,
          name: p.name,
          description: p.description,
          priceNum: p.price,
          priceRaw: p.price.toLocaleString('vi-VN'),
          categoryId: p.categoryId.toString(),
          imageUrl: p.imageUrl
        }));
      });
    }
  }, [isEdit, id]);

  /*  helper format VND  */
  const fmt = v =>
    v.replace(/\D/g,'').replace(/\B(?=(\d{3})+(?!\d))/g,'.');

  const handleChange = e => {
    const { name, value } = e.target;

    if (name === 'priceRaw') {
      const raw = fmt(value);
      setForm(f => ({ ...f,
        priceRaw: raw,
        priceNum: parseFloat(raw.replace(/\./g,'')) || 0
      }));
    } else {
      setForm(f => ({ ...f, [name]: value }));
    }
  };

  const handleFile = e =>
    setForm(f => ({ ...f, imageFile: e.target.files?.[0] || null }));

  /*  submit  */
  const handleSubmit = async e => {
    e.preventDefault();

    // kim tra client
    if (!form.categoryId) {
      alert('Bn cha chn danh mc'); return;
    }
    if (form.priceNum <= 0) {
      alert('Gi phi ln hn 0'); return;
    }

    const data = new FormData();
    data.append('Name',        form.name);
    data.append('Description', form.description);
    data.append('Price',       form.priceNum);          // s sch
    data.append('CategoryId',  form.categoryId);        // m bo c gi tr
    data.append('ImageUrl',    form.imageUrl);

    if (form.imageFile) data.append('File', form.imageFile);

    try {
      if (isEdit)
        await api.put(`/api/products/${id}`, data);
      else
        await api.post('/api/products', data);

      navigate('/admin/products');
    } catch (err) {
      console.error(err);
      alert(err.response?.data?.detail ?? 'Khng lu c sn phm');
    }
  };

  return (
    <div className="card mx-auto" style={{maxWidth:600}}>
      <div className="card-body">
        <h5 className="card-title">{isEdit ? 'Chnh sa':'Thm mi'} sn phm</h5>

        <form onSubmit={handleSubmit}>
          {/* Danh mc */}
          <div className="mb-3">
            <label className="form-label">Danh mc</label>
            <select
              className="form-select"
              name="categoryId"
              value={form.categoryId}
              onChange={handleChange}
              required
            >
              <option value="" disabled>-- Chn danh mc --</option>
              {cats.map(c => (
                <option key={c.id} value={c.id}>{c.name}</option>
              ))}
            </select>
          </div>

          {/* Tn */}
          <div className="mb-3">
            <label className="form-label">Tn</label>
            <input
              className="form-control"
              name="name"
              value={form.name}
              onChange={handleChange}
              required
            />
          </div>

          {/* M t */}
          <div className="mb-3">
            <label className="form-label">M t</label>
            <textarea
              className="form-control"
              name="description"
              value={form.description}
              onChange={handleChange}
            />
          </div>

          {/* Gi */}
          <div className="mb-3">
            <label className="form-label">Gi ()</label>
            <input
              className="form-control"
              name="priceRaw"
              value={form.priceRaw}
              onChange={handleChange}
              required
            />
          </div>

          {/* nh */}
          <div className="mb-3">
            <label className="form-label">nh sn phm</label>
            <input type="file" accept="image/*"
                   className="form-control"
                   onChange={handleFile}/>
            {form.imageUrl && (
              <img src={form.imageUrl} alt="preview"
                   className="img-thumbnail mt-2" style={{maxWidth:120}}/>
            )}
          </div>

          <button className="btn btn-primary" type="submit">
            {isEdit ? 'Cp nht' : 'Thm'}
          </button>
          <button className="btn btn-secondary ms-2" type="button"
                  onClick={() => navigate('/admin/products')}>
            Hu
          </button>
        </form>
      </div>
    </div>
  );
}
