import React, { useEffect, useState} from 'react';
import { api } from '../api/api';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';


export default function MyOrdersPage() {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const { token } = useAuth();
  const nav = useNavigate();

  useEffect(() => {
    if (!token) {
      nav('/login');
      return;
    }
    api.get('/api/orders/my')
      .then(res => setOrders(res.data))
      .catch(() => setOrders([]))
      .finally(() => setLoading(false));
  }, [token, nav]);

  if (loading) return <div className="text-center mt-5">ang ti n hng...</div>;

  return (
    <div className="container" style={{ maxWidth: 800 }}>
      <h3 className="my-4 text-center">Lch s n hng ca ti</h3>
      {orders.length === 0 && <div className="alert alert-info">Bn cha c n hng no.</div>}
      {orders.map(order => (
        <div className="card mb-4 shadow-sm" key={order.id}>
          <div className="card-header">
            <b>n #{order.id}</b> &nbsp; | &nbsp; Ngy t: {new Date(order.createdAt).toLocaleString()}
          </div>
          <div className="card-body p-2">
            <b>Khch:</b> {order.customerName} &nbsp; | &nbsp; Email: {order.customerEmail}
            <table className="table table-sm table-bordered mt-2">
              <thead>
                <tr>
                  <th>Sn phm</th>
                  <th>S lng</th>
                  <th>n gi</th>
                  <th>Thnh tin</th>
                </tr>
              </thead>
              <tbody>
                {order.items.map(item => (
                  <tr key={item.id}>
                    <td>{item.product?.name || '---'}</td>
                    <td>{item.quantity}</td>
                    <td>{item.product?.price?.toLocaleString() || ''}</td>
                    <td>{(item.product?.price * item.quantity)?.toLocaleString() || ''}</td>
                  </tr>
                ))}
              </tbody>
            </table>
            <div className="text-end pe-3"><b>Tng tin:</b> {order.items.reduce((sum, i) => sum + (i.product?.price * i.quantity || 0), 0).toLocaleString()} </div>
          </div>
        </div>
      ))}
    </div>
  );
}
